@media(max-width:575.9px) {
  body {
    padding-top: 45px;
  }

  .navbar-menu {
    position: absolute;
    right: 18px;
    top: 13px;
    width: 26px;
    transition: all 0.3s cubic-bezier(0.7, 0, 0.3, 1) 0s;
    height: 20px;
    z-index: 12;
    cursor: pointer;

    span {
      background-color: #000;
      display: block;
      height: 2px;
      margin-top: -1px;
      position: relative;
      top: 50%;
      transition: all 0.3s cubic-bezier(0.7, 0, 0.3, 1) 0s;
      width: 100%;

      &:after,
      &:before {
        background-color: #000;
        content: "";
        display: block;
        height: 2px;
        left: 0;
        position: absolute;
        transition: all 0.3s cubic-bezier(0.7, 0, 0.3, 1) 0s;
        width: 100%;
      }

      &:after {
        top: 7px;
      }

      &:before {
        top: -7px;
      }
    }
  }

  .navbar-menu-active {
    span {
      background: none;

      &:after {
        transition: all 0.3s ease 0s;
        transform: translateY(0px) rotate(45deg);
        top: 0;
      }

      &:before {
        transition: all 0.3s ease 0s;
        transform: translateY(0px) rotate(-45deg);
        top: 0;
      }
    }
  }
  /* navbar */
  .navbar {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    background: rgba(255,255,255,0.8);
    z-index: 999;
    padding: 0 15px;

    .container {}

    .row {
      align-items: center;
      height: 45px;
    }
  }

  .navbar-brand {
    display: flex;
    align-items: center;

    img {
      display: block;
      max-height: 30px;
    }
  }

  .navbar-nav-btn {
    position: absolute;
    left: 0;
    top: 45px;
    width: 100%;
    background: #fff;
    padding: 0 15px;
    display: none;
  }

  .navbar-nav {
    li {
      line-height: 40px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.08);

      a {
        color: #777;
        padding: 0 10px;
        display: block;

        &:hover {
          background: rgba(0, 0, 0, 0.08);
        }
      }
    }
  }

  .navbar-btn {
    a {
      color: #777;
      display: block;
      line-height: 40px;
      padding: 0 10px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.08);

      &:hover {
        background: rgba(0, 0, 0, 0.08);
      }
    }
  }
  /* realtime-feeling */
  .realtime-feeling {
    background: url("../images/realtime.png") no-repeat center center;
    background-size: cover;
    padding: 35px 0;

    .container {
      //  padding: 0 20px;
    }
  }

  .realtime-feeling-titel {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 40px;

    h2 {
      font-weight: 400;
      font-size: 16px;
    }

    a {
      font-size: 12px;
      color: #fff!important;
      text-decoration: underline!important;

      &:hover {
        color: #fff!important;
        opacity: 0.9;
      }
    }
  }

  .realtime-feeling-container {
    padding: 5px 0;
    border-top: 1px dashed #fff;
    border-bottom: 1px dashed #fff;
  }

  .realtime-swiper {
    height: 280px;

    .swiper-slide {
      height: auto;

      p {
        font-size: 14px;
        line-height: 20px;
      }
    }
  }

  .realtime-app-download {
    float: right;
    max-width: 90px;
    display: none;

    img {
      width: 100%;
    }

    p {
      font-size: 14px;
      color: #fff;
      text-align: left;
    }
  }

  .classification-type-tabs {
    position: relative;

    .type-title {
      font-size: 16px;
      border: 1px solid #ddd;
      padding: 0 15px;
      margin-top: 10px;

      p {
        height: 40px;
        line-height: 40px;
        display: -webkit-flex;
        display: -ms-flex;
        display: flex;
        align-items: center;
        justify-content: space-between;

        &:after {
          content: '';
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 6px 6px 0 6px;
          border-color: #333 transparent transparent transparent;
        }
      }
    }

    .type-list {
      position: absolute;
      left: 0;
      top: 40px;
      width: 100%;
      padding: 0 15px;
      z-index: 9;
      display: none;

      ul {
        background: #fff;
        z-index: 9;
        border: 1px solid #ddd;
        padding: 0 15px;

        li {
          font-size: 14px;
          line-height: 40px;
          border-bottom: 1px solid rgba(0, 0, 0, 0.08);

          &:last-child {
            border-bottom: none;
          }

          a {
            color: #777;
            transition: all 0.3s linear;

            &:before {
              content: '-';
              font-size: 16px;
              margin-right: 5px;
            }
          }

          &.active,
          &:hover {
            a {
              color: #F2812A!important;

              &:after {
                transform: rotate(180deg);
              }
            }
          }
        }
      }
    }
  }

  .classification-swiper-container {
    padding: 10px 0;
  }

  .classification-swiper {
    .swiper-slide {
      li {
        float: left;
        width: 50%;
        font-size: 16px;
        line-height: 40px;
        padding: 0 5px;
        box-sizing: border-box;

        a {
          color: #333;
        }
      }
    }
  }

  .situation-report {
    background: #00649a;
    padding: 40px 0;

    .col-pic {
      text-align: center;
      width: 100%;

      img {
        display: block;
        max-width: 100%;
        margin: 0 auto;
      }

      .img1 {
        width: 32%;
      }

      .img2 {
        margin-top: 20px;
        width: 80%;
      }
    }

    .col-text {
      text-align: center;
      width: 100%;
      margin-top: 40px;

      img {
        max-width: 100%;
        display: block;
        margin: 0 auto;
      }

      p {
        font-size: 16px;
        color: #FFFFFF;
        margin-top: 10px;
      }
    }
  }

  .copyright {
    background: #003d5d;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    padding: 15px 0;
    color: #fff;

    span {
      display: block;
    }

    a {
      color: #fff;
    }
  }

  .security-records {
    text-align: center;
    line-height: 40px;
    font-size: 16px;
    color: #AA93A0;

    .row {
      justify-content: center;
    }

    img,
    span {
      display: inline-block;
      vertical-align: middle;
    }

    img {
      margin-right: 10px;
    }

    a {
      color: #AA93A0;
    }
  }
}
