@import 'mixin','index-xs','index-sm','index-md','index-lg','index-xl';
@include media-breakpoint-up(sm) {
  body {
    padding-top: 60px;
  }
  /* navbar */
  .navbar {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    background: #fff;
    z-index: 999;

    .row {
      align-items: center;
      height: 60px;
    }
  }

  .navbar-brand {
    display: flex;
    align-items: center;

    img {
      display: block;
      max-width: 100px;
    }
  }

  .navbar-nav-btn {
    display: flex!important;
    align-items: center;
    flex: 1;
  }

  .navbar-nav {
    flex: 1;
    margin-left: 8%;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;

    li {
      color: #000;
      float: left;
      line-height: 40px;
      margin-right: 25px;
      font-size: 14px;

      &:last-child {
        margin-right: 0;
      }

      a {
        color: #000;
        position: relative;
        display: block;
        height: 40px;

        &:after {
          content: '';
          position: absolute;
          left: 50%;
          width: 0;
          background: $mainColor;
          height: 1px;
          opacity: 0;
          bottom: 0;
          transition: all 0.3s ease 0s;
          transform: translateX(-50%);
        }
      }

      &.active,
      &:hover {
        a {
          color: $mainColor;

          &:after {
            opacity: 1;
            width: 100%;
          }
        }
      }
      @include media-breakpoint-up(md) {
        font-size: 16px;
        margin-right: 35px;
      }
    }
  }
  @include media-breakpoint-up(md) {
    margin-left: 10%;

    li {
      font-size: 16px;
      margin-right: 30px;
    }
  }

  .navbar-btn {
    display: flex;
    align-items: center;

    a {
      float: left;
      height: 30px;
      border: 1px solid #F2812A;
      line-height: 28px;
      width: 70px;
      text-align: center;
      font-size: 14px;
      color: #F2812A;
      margin-left: 20px;
      transition: all 0.3s ease 0s;

      &.active,
      &:hover {
        background: #F2812A;
        color: #fff!important;
      }
    }
  }
  /* realtime-feeling */
  .realtime-feeling {
    background: url("../images/realtime.png") no-repeat center center;
    background-size: cover;
    padding: 50px 0;

    .container {
      //  padding: 0 20px;
    }
  }

  .realtime-feeling-titel {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 40px;

    h2 {
      font-weight: 400;
      font-size: 20px;
    }

    a {
      font-size: 16px;
      color: #fff!important;
      text-decoration: underline!important;

      &:hover {
        color: #fff!important;
        opacity: 0.9;
      }
    }
  }

  .realtime-feeling-container {
    padding: 5px 0;
    border-top: 1px dashed #fff;
    border-bottom: 1px dashed #fff;
  }

  .realtime-swiper {
    height: 280px;

    .swiper-slide {
      height: auto;

      p {
        font-size: 16px;
        line-height: 20px;
      }
    }
  }

  .realtime-app-download {
    float: right;
    max-width: 90px;
    display: none;

    img {
      width: 100%;
    }

    p {
      font-size: 14px;
      color: #fff;
      text-align: left;
    }
    @include media-breakpoint-up(lg) {
      display: block;
    }
  }

  .classification-type-tabs {
    background: #edf2f6;

    .container {
      align-items: center;
    }

    .type-title {
      display: none;
    }

    .type-list {
      display: block!important;
    }

    ul {
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      align-items: center;
      height: 50px;
      justify-content: center;

      li {
        flex: 1;
        font-size: 14px;
        color: #00649A;
        text-align: center;

        a {
          color: #00649A;
          transition: all 0.3s linear;

          &:after {
            content: '';
            display: inline-block;
            width: 8px;
            height: 8px;
            background: url("../images/u47.png") no-repeat center center;
            background-size: cover;
            margin-left: 3px;
          }
        }

        &.active,
        &:hover {
          a {
            color: #F2812A!important;

            &:after {
              transform: rotate(180deg);
            }
          }
        }
      }
    }
    @include media-breakpoint-up(md) {
      ul {
        li {
          font-size: 16px;

          a {
            &:after {
              width: 12px;
              height: 12px;
              background: url("../images/u47.png") no-repeat center center;
              background-size: cover;
            }
          }
        }
      }
    }
  }

  .classification-swiper-container {
    padding: 30px 20px;
    overflow: hidden;
    @include media-breakpoint-up(md) {}
  }

  .classification-swiper {
    .swiper-slide {
      li {
        float: left;
        width: 33.33%;
        font-size: 14px;
        line-height: 36px;

        a {
          color: #333;
        }
        @include media-breakpoint-up(md) {
          width: 25%;
          font-size: 16px;
        }
        @include media-breakpoint-up(lg) {
          width: 12.5%;
        }
      }
    }
  }

  .situation-report {
    background: #00649a;
    padding: 60px 0;

    .col-pic {
      img {
        display: block;
        max-width: 100%;
      }

      .img2 {
        margin-top: 30px;
      }
      @include media-breakpoint-down(sm) {
        width: 100%;

        img {
          display: block;
          max-width: 100%;
          margin: 0 auto;
        }

        .img1 {
          width: 32%;
        }

        .img2 {
          width: 80%;
          font-size: 19px;
        }
      }
      @include media-breakpoint-up(md) {
        .img2 {
          margin-top: 30px;
          font-size: 18px;
        }
      }
    }

    .col-text {
      text-align: center;

      img {
        max-width: 100%;
        display: block;
        margin: 0 auto;
      }

      p {
        font-size: 16px;
        color: #FFFFFF;
        margin-top: 10px;
      }
      @include media-breakpoint-down(sm) {
        margin-top: 40px;
        justify-content: center;
        width: 100%;
      }
    }
    @include media-breakpoint-up(md) {
      padding: 80px 0;

      .col-pic {
        flex: 1;

        img {
          display: block;
          max-width: 100%;
        }

        .img2 {
          margin-top: 30px;
        }
      }

      .col-text {
        text-align: center;
        width: auto;

        img {
          max-width: 100%;
        }

        p {
          font-size: 18px;
          color: #FFFFFF;
          margin-top: 10px;
        }
      }
    }
  }

  .copyright {
    background: #003d5d;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    padding: 20px 0;
    color: #fff;

    .row {
      justify-content: center;
    }

    a {
      color: #fff;
    }
  }

  .security-records {
    text-align: center;
    line-height: 42px;
    font-size: 18px;
    color: #AA93A0;

    .row {
      justify-content: center;
    }

    img,
    span {
      display: inline-block;
      vertical-align: middle;
    }

    img {
      margin-right: 10px;
    }

    a {
      color: #AA93A0;
    }
  }
}
@include media-breakpoint-up(lg) {
  body {
    padding-top: 83px;
  }
  /* navbar */
  .navbar {
    .row {
      align-items: center;
      height: 83px;
    }
  }

  .navbar-brand {
    display: flex;
    align-items: center;

    img {
      display: block;
    }
  }

  .navbar-nav {
    flex: 1;
    margin-left: 15%;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;

    li {
      font-size: 20px;
      color: #000;
      float: left;
      line-height: 50px;
      margin-right: 50px;

      a {
        color: #000;
        position: relative;
        display: block;
        height: 50px;

        &:after {
          content: '';
          position: absolute;
          left: 50%;
          width: 0;
          background: $mainColor;
          height: 1px;
          opacity: 0;
          bottom: 0;
          transition: all 0.3s ease 0s;
          transform: translateX(-50%);
        }
      }

      &.active,
      &:hover {
        a {
          color: $mainColor;

          &:after {
            opacity: 1;
            width: 100%;
          }
        }
      }
    }
  }

  .navbar-btn {
    display: flex;
    align-items: center;

    a {
      float: left;
      height: 50px;
      border: 1px solid #F2812A;
      line-height: 48px;
      width: 100px;
      text-align: center;
      font-size: 20px;
      color: #F2812A;
      margin-left: 30px;
      transition: all 0.3s ease 0s;

      &.active,
      &:hover {
        background: #F2812A;
        color: #fff!important;
      }
    }
  }
}
